body {
	background-color: #131418;
	margin: 0;
	padding: 0;
	text-align: center;
	overflow: hidden;
  }
  .slide-container {
	margin: 50vh auto 0 auto;
	transform: translateY(-50%);
	width: 600px;
	height: 450px;
	overflow: hidden;
	border-radius: 30px;
  }
  img {
	width: 20%;
  }
  
  .image-container {
	display: inline-block;
	width: 3000px;
	height: 400px;
	position: relative;
	transition: left 1s;
	
	animation: slide 15s infinite;
  }
  .slider-image {
	float: left;
  }
  
  .button-container {
	position: relative;
	top: 10px;
  }
  
  .slider-button {
	height: 10px;
	width: 10px;
	margin: 4px;
	display: inline-block;
	border-radius: 5px;
	background-color: rgba(white, 0.5);
  }
  .slider-button:hover {
	background-color: rgba(white, 0.7);
  }
  
  #slider1:target ~ .image-container {
	left: 0px;
  }
  #slider2:target ~ .image-container {
	left: -600px;
  }
  #slider3:target ~ .image-container {
	left: -1200px;
  }
  #slider4:target ~ .image-container {
	left: -1800px;
  }
  #slider5:target ~ .image-container {
	left: -2400px;
  }
  
  .image-container:hover {
  }
  
  @keyframes slide {
	0% {
		left: 0;
	}
	20% {
		left: -600px;
	}
	40% {
		left: -1200px;
	}
	60% {
		left: -1800px;
	}
	80% {
		left: -2400px;
	}
	100% {
		left: 0;
	}
  }